/* ==========
LANDING PAGE CARD TEMPLATE
========== */

// Landing intro slide
.folio-landing {
  @include flex-column;
  justify-content: center;
  align-items: center;

  background-color: $primary-1;
  width: calc(100vw - 16rem);
  height: calc(100vh - 8rem);
  margin: 4rem 8rem;
  box-sizing: border-box;

  .folio-intro {
    color: #fff;
    width: 100%;
    max-width: 76rem;

    .text {
      width: 100%;
      max-width: 64rem;
      margin: auto;
    }

    p:first-child {
      font-size: 2.4rem;
      line-height: 1.6;
    }

    p {
      font-size: 1.6rem;
      line-height: 1.6;
    }
  }
}

// close folio-landing
