/* ==========
LANDING PAGE 
========== */

// IEP
.folio-landing.modify-iep {
  justify-content: center;
  align-items: flex-start;
  width: 80vw;
  padding: 4rem 0 4rem 8rem;
  background: rgb(214, 233, 208);
  background: linear-gradient(
    90deg,
    rgba(214, 233, 208, 1) 0%,
    rgba(168, 209, 240, 1) 100%
  );
  position: relative;

  .content {
    width: 100%;
    max-width: 60rem;
    padding-top: 4rem;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 0.8rem;
      width: 7.2rem;
      background-color: #47c8bc;
    }
  }

  .category {
    font-weight: 600;
    font-size: 1.8rem;
    line-height: 1;
    margin-bottom: 1.2rem;
    display: block;
  }

  .title {
    font-size: 4.8rem;
    line-height: 1.1;
    margin-top: 0;
    margin-bottom: 3.2rem;
  }

  .para {
    font-size: 1.8rem;
    line-height: 1.4;
  }

  .link {
    &:after {
      background-color: #47c8bc;
    }
    &:hover {
      .fa-angle-right {
        color: #47c8bc;
      }
    }
  }

  .btn-row {
    margin-top: 4rem;
  }

  .logos {
    position: absolute;
    top: 0;
    right: 0;
    padding: 2.4rem 4rem;
    @include flex-row;
    justify-content: space-between;
    width: 100%;
    max-width: 24rem;

    .logoIngenio {
      margin-top: 0.6rem;
    }
  }

  .screen {
    position: absolute;
    top: 12rem;
    right: calc(-20vw + 16rem);
    z-index: 1;
    width: 72rem;
    height: auto;
    border-radius: 0.4rem;
    overflow: hidden;
    box-shadow: 0.2rem 0.2rem 0.8rem 0 rgba(00, 00, 00, 0.2);

    img {
      width: 100%;
      display: block;
    }
  }

  .thumb {
    position: absolute;
    bottom: 10rem;
    right: -0.8rem;
    border: 8px solid #fff;
    border-radius: 0.8rem 0 0 0.8rem;
  }
}
