/* ==========
CASE STUDY IEP Intro
========== */

.card-iep-intro {
  @include flex-column;
  justify-content: center;

  background-color: $primary-1;
  width: calc(100vw - 16rem);
  height: calc(100vh - 8rem);
  margin: 4rem 8rem;
  box-sizing: border-box;

  padding: 4rem 8rem;
  color: #fff;
  background: #000;
  position: relative;

  .content-row {
    @include flex-row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }

  .content {
    width: 100%;
    max-width: 60rem;
    padding-top: 4rem;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 0.8rem;
      width: 7.2rem;
      background-color: #47c8bc;
    }
  }

  .title {
    font-size: 4rem;
    line-height: 1.1;
    margin-top: 0;
    margin-bottom: 3.2rem;
  }

  .para {
    font-size: 1.8rem;
    line-height: 1.4;
  }

  .link {
    &:after {
      background-color: #47c8bc;
    }

    &:hover {
      .fa-angle-right {
        color: #47c8bc;
      }
    }
  }

  .btn-row {
    margin-top: 4rem;
  }

  .logos {
    position: absolute;
    top: 0;
    right: 0;
    padding: 2.4rem 4rem;
    @include flex-row;
    justify-content: space-between;
    width: 100%;
    max-width: 24rem;

    .logoIngenio {
      margin-top: 0.6rem;
    }
  }

  .pullquote {
    color: #fff;
    font-size: 3.2rem;
    font-weight: 700;
    width: 100%;
    max-width: 40rem;
  }
}