/* ==========
ANIMATION FOR INTRO LANDING PAGE
========== */

.folio-intro-illustration {
  @include flex-row;
  justify-content: space-between;

  width: 100%;
  padding: 0;
  margin: 8rem 0;

  label {
    font-size: 1.6rem;
    font-weight: 500;
    text-align: center;
    display: block;
  }
}

.intro-circles {
  @include flex-column;
  justify-content: center;
  align-content: center;

  width: 20rem;
  height: 20rem;
  position: relative;

  opacity: 0;

  -webkit-animation: circle-full-ani;
  -webkit-animation-name: circle-full-ani;
  animation-name: circle-full-ani;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-timing-function: ease-out;
  animation-timing-function: ease-out;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;

  i {
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 20rem;
    height: 20rem;
    border-radius: 50%;
    border-width: 2px;
    border-style: solid;
    border-color: transparent;
  }

  .top {
    border-top-color: $accent-1;

    -webkit-animation: circle-top-ani;
    -webkit-animation-name: circle-top-ani;
    animation-name: circle-top-ani;
    -webkit-animation-duration: 4s;
    animation-duration: 4s;
    -webkit-animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }

  .right {
    border-right-color: $accent-2;

    -webkit-animation: circle-right-ani;
    -webkit-animation-name: circle-right-ani;
    animation-name: circle-right-ani;
    -webkit-animation-duration: 4s;
    animation-duration: 4s;
    -webkit-animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }

  .bottom {
    border-bottom-color: $accent-3;

    -webkit-animation: circle-bottom-ani;
    -webkit-animation-name: circle-bottom-ani;
    animation-name: circle-bottom-ani;
    -webkit-animation-duration: 4s;
    animation-duration: 4s;
    -webkit-animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }

  .left {
    border-left-color: $accent-4;

    -webkit-animation: circle-bottom-ani;
    -webkit-animation-name: circle-bottom-ani;
    animation-name: circle-bottom-ani;
    -webkit-animation-duration: 4s;
    animation-duration: 4s;
    -webkit-animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    animation-timing-function: cubic-bezier(0, 0.37, 0.29, 1);
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
  }
}

.circle01 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}
.circle02 {
  -webkit-animation-delay: 0.8s;
  animation-delay: 0.8s;
}
.circle03 {
  -webkit-animation-delay: 1.2s;
  animation-delay: 1.2s;
}
.circle04 {
  -webkit-animation-delay: 1.6s;
  animation-delay: 1.6s;
}

//@-webkit-keyframes circle-full-ani
@-webkit-keyframes circle-full-ani {
  0% {
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
@keyframes circle-full-ani {
  0% {
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@-webkit-keyframes circle-top-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(2160deg);
  }
}

@keyframes circle-top-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(2160deg);
  }
}

@-webkit-keyframes circle-right-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(2880deg);
  }
}

@keyframes circle-right-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(2880deg);
  }
}

@-webkit-keyframes circle-bottom-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(3600deg);
  }
}

@keyframes circle-bottom-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(3600deg);
  }
}

@-webkit-keyframes circle-left-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(4320deg);
  }
}

@keyframes circle-left-ani {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(4320deg);
  }
}
