/* ==========
BUTTON AND LINK STYLES
========== */

.btn-row {
  display: block;
}

.link {
  display: inline-block;
  padding: 1.6rem 4rem 1.6rem 0.2rem;
  border-bottom: 1px solid #fff;
  position: relative;
  line-height: 1;
  text-decoration: none;

  @include transition02;

  &:after {
    content: "";
    width: 100%;
    height: 0.4rem;
    position: absolute;
    bottom: -0.1rem;
    left: 0;
    transform: scaleX(0);
    transform-origin: left;
    @include transition02;
  }

  &:hover {
    cursor: pointer;

    &:after {
      transform: scaleX(1);
    }
  }

  .fa-angle-right {
    position: absolute;
    right: 0;
    @include transition02;
  }
}
