/* ==========
CASE STUDY IEP Designs
========== */

.card-iep-designs {
    justify-content: center;
    align-items: flex-start;
    padding: 8rem 8rem 8rem 16rem;
    position: relative;

    .content-row {
        @include flex-row;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        margin-top: 4rem;
    }

    .content {
        width: 100%;
        max-width: 48rem;
    }

    .category {
        font-weight: 600;
        font-size: 1.8rem;
        line-height: 1;
        margin-bottom: 1.2rem;
        display: block;
    }

    .title {
        max-width: 60rem;
        color: #000dd0;
        font-size: 4rem;
        line-height: 1.1;
        margin-top: 3.2rem;
        margin-bottom: 2.4rem;
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: -3.2rem;
            left: 0;
            height: 0.8rem;
            width: 7.2rem;
            background-color: #808080;
        }
    }

    .para {
        font-size: 1.8rem;
        line-height: 1.4;
    }

    .img-iep-wire {
        width: 40rem;
        height: 22rem;
        border-radius: .4rem;
        overflow: hidden;
        @include shadow01;
    }
}