/* ==========
TYPOGRAPHY
========== */

html {
  font-size: 62.5%;
  font-weight: 400;
  font-style: normal;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: $primary-1;
  font-size: 1.4rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-family: "Helvetica Neue";
  font-weight: 600;
  font-style: normal;
}

h1 {
  font-size: 8rem;
}
h2 {
  font-size: 6rem;
}
h3 {
  font-size: 1.6rem;
}
h4 {
  font-size: 1.4rem;
}

p {
  font-size: 1.4rem;
}

a {
  color: $primary-1;
  position: relative;
}

// h2 level
.folio-view-title {
  margin-top: 1.6rem;
  margin-bottom: 1.6rem;
  font-weight: normal;
}
// h3 level
.folio-section-title {
  margin-bottom: 2rem;
}
// h4 level
.folio-component-title {
  font-size: 1.8rem;
  font-weight: normal;
  line-height: 1.8;
  margin-bottom: 0.8rem;

  small {
  }
}
// h5 level
.folio-block-title {
  @extend .folio-component-title;
  font-size: 1.4rem;
}

// intro landing
.folio-intro-title {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 1rem;
  text-align: center;
}
